import { createContext, useContext, useState, useEffect } from "react";
import Axios from "axios";

const Identity = createContext([{active: null, users:[]}, undefined]);




export class IdentityProvider extends React.Component{
    constructor(props) {
        super(props);

        this.state= {
            id: {active: null, users: []},
            axios: undefined
        };

        this.beforeMount();
    }

    request = (req) => {
        const ide = this.state.id;
        if(Number.isInteger(ide.active) && ide.users && ide.users[ide.active]) {
            console.log("token supplied");
            req.headers.Authorization = `Bearer ${ide.users[ide.active].accessToken}`;
        }
        return req;
    }

    response = (res) => {
        if(res.status === 401) {
            var no = res.headers["Www-Authenticate"].split(",");
            ///...

            ///removeUser(id.users[id.active].idString);
        } 
        return res;
    }

    reject = (error) => {
        // Do something with request error
        return Promise.reject(error);
    }

    addUser = (idString, username, accessToken) => {
        this.setState ((prev) => {
            var newO = {...prev.id};

            var index = newO.users.findIndex((val) => val.idString = idString);
            if(index===-1)
                newO.users = [...newO.users, {idString, username, accessToken}];
            
            return {id: newO};
        });
    }

    removeUser= (idString) => {
        this.setState((prev) => {
            var newO = {...prev.id};

            var index = newO.users.findIndex((val) => val.idString = idString);
            if(index>-1)
                newO.users = newO.users.slice(index, index+1);
            
            return {id: newO};
        });
    }

    setActive = (idx) => {
        this.setState((prev) => {
            var newO = {...prev.id};

            if(idx > -1 && idx < prev.id.users.length || idx === null) {
                newO.active = idx;    
            } 
            return {id: newO};
        });
    }

    persist() {
        console.log("persist");
        localStorage.setItem("active", JSON.stringify(this.state.id.active));
        localStorage.setItem("users", JSON.stringify(this.state.id.users));
    }

    checkActive() {
        if(this.state.id.active !== null) {
            this.state.axios.get("/protected")
            .then((val) => {
                console.log("✔ Account ok: ",val);
            }, (reason)=>{
                console.log("❌ Account bad: ", reason);

                if(reason.response) {
                    var tokens = reason.response.headers["www-authenticate"].split(",").map(x => x.trim());

                    //decide what to do
                    if(tokens[1] && tokens[1].startsWith('error="')) {
                        var error = tokens[1].substring(7, tokens[0].length-2);
                        console.log("auth error");
                    }
                } else {
                    this.setState((prev) => {return {id:{...prev.id, active: null}}});
                }
            })
        }
    }

    beforeMount() {
        if(typeof window === 'undefined') {
            return;
        }
        console.log("before");

        //get initial data
        var active,users;

        try {
            active = JSON.parse(localStorage.getItem("active") || "null");  
        } catch (error) {
            active= this.state.id.active;
        }
        try {
            users = JSON.parse(localStorage.getItem("users") || "[]");
        } catch (error) {
            users= this.state.id.users;
        }
        

        const instance = Axios.create({
            baseURL: "https://drkslv.herokuapp.com"
        });

        instance.interceptors.request.use(this.request, this.reject);
        instance.interceptors.response.use(this.response, this.reject);

        //pass context down
         
        this.state.id= {active, users, addUser: this.addUser, removeUser: this.removeUser, setActive: this.setActive};
        this.state.axios = instance;

        this.checkActive();
        //this.setState(prev=> {return {id: {active, users, addUser: this.addUser, removeUser: this.removeUser, setActive: this.setActive}, axios: instance}});
    }

        


    render() {
        if(typeof window !== 'undefined') {
            this.persist();
        }
        return <Identity.Provider value={[this.state.id, this.state.axios]}>
            {this.props.children}
        </Identity.Provider>;
    }
        
}
    

export const useIdentity = () => {
    return useContext(Identity);
}

